import React, { useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

const AltaZapatilla = (props) => {
	const altaZapatilla = props.onNuevaZapatilla;
	const [error, setError] = useState(false);

	const [nuevaMarca, setNuevaMarca] = useState('');
	const [nuevaImagen, setNuevaImagen] = useState('');
	const [nuevoModelo, setNuevoModelo] = useState('');
	const [nuevoPrecio, setNuevoPrecio] = useState();

	const gestorSubmit = (e) => {
		e.preventDefault();
		setError(false);
		if (
			nuevaMarca.trim() === '' ||
			nuevaImagen.trim() === '' ||
			nuevoModelo.trim() === '' ||
			nuevoPrecio === ''
		) {
			setError(true);
			return;
		}
		//	Creamos
		const nuevaZapatilla = {
			id: uuidv4(),
			marca: nuevaMarca,
			image: nuevaImagen,
			modelo: nuevoModelo,
			precio: nuevoPrecio,
		};
		altaZapatilla(nuevaZapatilla);

		// Limpiamos
		setNuevaMarca('');
		setNuevaImagen('');
		setNuevoModelo('');
		setNuevoPrecio('');
	};

	const gestorCambioMarca = (event) => {
		setNuevaMarca(event.target.value);
	};
	const gestorCambioImagen = (event) => {
		setNuevaImagen(event.target.value);
	};
	const gestorCambioModelo = (event) => {
		setNuevoModelo(event.target.value);
	};
	const gestorCambioPrecio = (event) => {
		setNuevoPrecio(event.target.value);
	};

	return (
		<div className='card border-success'>
			
			<div class="card-header"><h2 className="text-center" >Añada una nueva zapatilla</h2></div>
			<div className="card-body">	
				<span><a className="text-danger" href="https://www.sneakersnstuff.com/en/1279/sneakers-on-sale?orderBy=Popularity" target="_blank" rel="noopener noreferrer">Página referente</a></span>
				{error ? (
					<h3 className='error-campos'>Debe completar todos los campos</h3>
				) : null}
				<form className="form" onSubmit={gestorSubmit} action=''>
					<div className="form-group">
						<div className="form-floating mb-3">
							<input
								onChange={gestorCambioMarca}
								className='form-control form-text'
								type='text'
								name='marca'
								id='marca'
								placeholder='Marca'
								required
								value={nuevaMarca}
							/>
							<label for="marca">Marca</label>
						</div>
					</div>
					<div className="form-group">
						<div className="form-floating mb-3">
							<div className="form-floating">
								<input 
									onChange={gestorCambioImagen}
									className='form-control form-text'
									type="text" 
									name="image" 
									id="image" 
									placeholder='Imagen(URL)'
									required
									value={nuevaImagen}
								/>
								<label for="image">Imagen - URL</label>
							</div>
						</div>
					</div>
					<div className="form-group">
						<div className="form-floating mb-3">
							<div className="form-floating">
								<input
									onChange={gestorCambioModelo}
									className='form-control form-text'
									type='modelo'
									name='modelo'
									id='modelo'
									placeholder='Modelo'
									required
									value={nuevoModelo}
								/>
								<label for="modelo">Modelo</label>
							</div>
						</div>
					</div>
					<div className="form-group">
						<div className="form-floating mb-3">
							<div className="form-floating">
								<input
									onChange={gestorCambioPrecio}
									className='form-control form-text'
									type='number'
									name='precio'
									id='precio'
									placeholder='Precio'
									required
									value={nuevoPrecio}
								/>
								<label for="precio">Precio</label>
							</div>
						</div>
					</div>
					<button className='btn btn-success w-100' type='submit'>
						Añadir
					</button>
				</form>
			</div>
		</div>
	);
};

export default AltaZapatilla
