import React from 'react';
import './MuestraZapatillas.css';

const MuestraZapatillas = (props) => {
    const zapatillas = props.listaZapatillas;
    return (
        <div>
            <h1>Lista de zapatillas registradas</h1>
       <div className="contiene-zapatilla">
        {zapatillas.map((item) => {
            return (
                <div className="a-box card">
                    <div className="img-container">
                        <div className="img-inner">
                            <div className="inner-skew">
                                <img src={item.image}/>
                            </div>
                        </div>
                    </div>
                    <div className="text-container">
                        <h3>{item.marca}</h3>
                        <div className="desc-model">
                        <span class="badge bg-dark mb-2">{item.modelo}</span>
                        </div>
                        <p className="text-dark"><strong><h4>Precio: {item.precio}€ </h4></strong></p>
                    </div>
                </div>
            );
        })}
      </div>
        </div>
        
    )
}

export default MuestraZapatillas

