import React, { useState } from 'react';
import {v4 as uuidv4} from 'uuid';
import './CreaNuevoUsuario.css'

const CreaNuevoUsuario = (props) => {
  const creaNuevoUsuario = props.onNuevoUsuario;
  const [error, setError] = useState(false);
    
      const [nuevoName, setNuevoName] = useState(['']);
      const [nuevoSurname, setNuevoSurname] = useState(['']);
      const [nuevoAuthor, setNuevoAuthor] = useState(['']);
      const [nuevoEmail, setNuevoEmail] = useState(['']);
      const [nuevoPassword, setNuevoPassword] = useState(['']);
  
      const gestorSubmit = (e) => {
          e.preventDefault();
          //Validación de formulario
          setError(false);
          if (
            nuevoName.trim() === '' ||
            nuevoSurname.trim() === '' ||
            nuevoAuthor.trim() === '' ||
            nuevoEmail.trim() === '' ||
            nuevoPassword.trim() === ''
          ){
            setError(true);
			      return;
          };
     
          //Creamos
          const nuevoUsuario =
              {
                  id: uuidv4(),
                  name: nuevoName,
                  surname: nuevoSurname,
                  author: nuevoAuthor,
                  email: nuevoEmail,
                  password: nuevoPassword,
              };
              creaNuevoUsuario(nuevoUsuario);
      
          // Limpiar formulario después de enviar los datos.
          setNuevoName('');
          setNuevoSurname('');
          setNuevoAuthor('');
          setNuevoEmail('');
          setNuevoPassword('');
              console.log(nuevoUsuario);
      };
  
      const gestorCambioNombre = (event) => {
        setNuevoName(event.target.value);
      };
      const gestorCambioApellidos = (event) => {
        setNuevoSurname(event.target.value);
      };
      const gestorCambioAutor = (event) => {
        setNuevoAuthor(event.target.value);
      };
      const gestorCambioEmail = (event) => {
        setNuevoEmail(event.target.value);
      };
      const gestorCambioPassword = (event) => {
        setNuevoPassword(event.target.value);
      };

      return (
          <div className="card border-primary nuevo-usuario">
            <div class="card-header"><h2 className="text-center">Registrar usuario</h2></div>
            <div class="card-body">
            <form className="form form-nuevo-usuario" onSubmit={gestorSubmit}>
                <div className="form-group">
						      <div className="form-floating mb-3">  
                    <input
                        onChange={gestorCambioNombre}
                        className='form-control form-text'
                        type='text'
                        name='name'
                        id='form-name'
                        placeholder='Nombre'
                        required
                        value={nuevoName}
                    />
                    <label for="name">Nombre</label>
                  </div>
					      </div>
                <div className="form-group">
						      <div className="form-floating mb-3">  
                    <input
                        onChange={gestorCambioApellidos}
                        className='form-control form-text'
                        type='text'
                        name='surname'
                        id='form-control form-surname'
                        placeholder='Apellidos'
                        required
                        value={nuevoSurname}
                    />
                    <label for="surname">Apellidos</label>
                  </div>
					      </div>
                <div className="form-group">
						      <div className="form-floating mb-3">
                    <input
                        onChange={gestorCambioAutor}
                        className='form-control form-text'
                        type='text'
                        name='author'
                        id='form-author-usr'
                        placeholder='Autor'
                        required
                        value={nuevoAuthor}
                    />
                    <label for="author">Alias</label>
                  </div>
					      </div>
                <div className="form-group">
						      <div className="form-floating mb-3">
                    <input
                        onChange={gestorCambioEmail}
                        className='form-control form-text'
                        type='email'
                        name='email'
                        id='form-email'
                        placeholder='Correo electrónico'
                        required
                        value={nuevoEmail}
                    />
                    <label for="email">Correo electrónico</label>
                  </div>
					      </div>
                <div className="form-group">
						      <div className="form-floating mb-3">
                    <input
                        onChange={gestorCambioPassword}
                        className='form-control form-text mb-1'
                        type='password'
                        name='password'
                        id='form-password'
                        placeholder='Contraseña'
                        required
                        value={nuevoPassword}
                    />
                    <label for="password">Contraseña</label>
                  </div>
					      </div>
                  <button className='btn btn-primary w-100 mb-2' type='submit'>
                      Registrar
                  </button>
              </form>
            </div>
          </div>
      )
}

export default CreaNuevoUsuario
