import React from 'react';
import './MuestraUsuarios.css'

const MuestraUsuarios = (props) => {
    let datosUsuarios = props.listaUsuarios;

    return (
        <div className="list-group muestra-usuarios">
            <h2 className="list-group-item list-group-item-action bg-info text-center">Usuarios</h2>
            <ul className="list-group-item list-group-item-action">
                {datosUsuarios.map((e) => {return <li key={e.id}>{e.name}</li>})}

            </ul>
        </div>
    )
}

export default MuestraUsuarios
